import { forwardRef } from 'react';

import Card from 'react-bootstrap/Card';

import { useIsMobile } from '../../hooks/useIsMobile';

const url =
  'https://images.pexels.com/photos/1402787/pexels-photo-1402787.jpeg?cs=srgb&dl=pexels-vlad-alexandru-popa-1402787.jpg&fm=jpg';

const sx = {
  width: window.innerWidth <= 375 ? '100%' : '660px',
  border: 'none',
  cardFooter: {
    border: 'none',
    backgroundColor: 'transparent',
    textAlign: 'start',
    span: {
      color: '#00a5db',
      fontWeight: 'bold',
    },
    arrows: {
      display: 'flex',
      justifyContent: 'center',
      gap: '20px',
      padding: '20px 0',
    },
  },
};

const CardComponent = forwardRef(function CardComponent(props, ref) {
  const isMobile = useIsMobile();

  return (
    <Card style={sx}>
      <Card.Img variant='top' src={url} />
      <Card.Body>
        <Card.Title>
          animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis
          est et expedita distinctio.
        </Card.Title>
        <Card.Text>
          blanditiis praesentium voluptatum deleniti atque corrupti quos dolores
          et quas molestias excepturi sint occaecati cupiditate non provident,
          similique sunt in culpa qui officia deserunt mollitia animi, id est
          laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita
          distinctio.
        </Card.Text>
      </Card.Body>
      {isMobile && (
        <Card.Footer style={sx.cardFooter}>
          <span style={sx.cardFooter.span}>READ MORE</span>
          <div style={sx.cardFooter.arrows}>
            <button onClick={() => ref.current.prev()}>
              <svg
                width='24'
                height='24'
                xmlns='http://www.w3.org/2000/svg'
                fillRule='evenodd'
                clipRule='evenodd'
                fill='#00a5db'
              >
                <path d='M20 .755l-14.374 11.245 14.374 11.219-.619.781-15.381-12 15.391-12 .609.755z' />
              </svg>
            </button>
            <button onClick={() => ref.current.next()}>
              <svg
                width='24'
                height='24'
                xmlns='http://www.w3.org/2000/svg'
                fillRule='evenodd'
                clipRule='evenodd'
                fill='#00a5db'
              >
                <path d='M4 .755l14.374 11.245-14.374 11.219.619.781 15.381-12-15.391-12-.609.755z' />
              </svg>
            </button>
          </div>
        </Card.Footer>
      )}
    </Card>
  );
});

